import "./App.css";
import HeaderContainer from "./components/Header/HeaderContainer";
import TestUIContainer from "./components/TestUI/TestUIContainer";
import MainPanelContainer from "./components/MainPanel/MainPanelContainer";
import { Route } from "react-router-dom";
import ServicePageContainer from "./components/ServicePanel/ServicePage/ServicePageContainer";
import UsersContainer from "./components/Users/UsersContainer";
import ProfileContainer from "./components/Profile/ProfileContainer";
import LoginPage from "./components/Login/Login";
import TestUI from "./components/TestUI/TestUI";
//LKhorosheva
const App = () => {
  return (
    <div>
      <HeaderContainer />
      <Route exact path="/" render={() => <TestUIContainer />} />
      <Route exact path="/ylab" render={() => <MainPanelContainer />} />
      <Route
        path="/servicepage/:name"
        render={() => <ServicePageContainer />}
      />
      <Route path="/users" render={() => <UsersContainer />} />
      <Route path="/profile/:userId?" render={() => <ProfileContainer />} />
      <Route path="/testui" render={() => <TestUIContainer />} />
      <Route path="/login" render={() => <LoginPage />} />
    </div>
  );
};

export default App;
