import * as React from "react";
import Paper from "@mui/material/Paper";
import Table from "@mui/material/Table";
import TableBody from "@mui/material/TableBody";
import TableCell from "@mui/material/TableCell";
import TableContainer from "@mui/material/TableContainer";
import TableHead from "@mui/material/TableHead";
import TablePagination from "@mui/material/TablePagination";
import TableRow from "@mui/material/TableRow";

interface Column {
  id: "activeLabels" | "running" | "done" | "exit" | "failed" | "pending";
  label: string;
  minWidth?: number;
  align?: "right";
  format?: (value: number) => string;
}

const columns: readonly Column[] = [
  { id: "activeLabels", label: "Active Labels", minWidth: 60 },
  {
    id: "running",
    label: "Running",
    minWidth: 60,
    align: "right",
    format: (value: number) => value.toFixed(2),
  },
  {
    id: "done",
    label: "Done",
    minWidth: 60,
    align: "right",
    format: (value: number) => value.toFixed(2),
  },
  {
    id: "exit",
    label: "Exit >0",
    minWidth: 60,
    align: "right",
    format: (value: number) => value.toFixed(2),
  },
  {
    id: "failed",
    label: "Failed",
    minWidth: 60,
    align: "right",
    format: (value: number) => value.toFixed(2),
  },
  {
    id: "pending",
    label: "Pending",
    minWidth: 60,
    align: "right",
    format: (value: number) => value.toFixed(2),
  },
];

interface Data {
  activeLabels: string;
  running: number;
  done: number;
  exit: number;
  failed: number;
  pending: number;
}

function createData(
  activeLabels: string,
  running: number,
  done: number,
  exit: number,
  failed: number,
  pending: number
): Data {
  return { activeLabels, running, done, exit, failed, pending };
}

const rows = [
  createData("wuthout label", 234567, 987345678, 23456, 345, 88234567),
  createData("project=bcd100", 17, 34567, 89, 20, 205),
  createData("project=bcd104", 0, 1024, 0, 7, 0),
];

export default function TableTasks() {
  const [page, setPage] = React.useState(0);
  const [rowsPerPage, setRowsPerPage] = React.useState(5);

  const handleChangePage = (event: unknown, newPage: number) => {
    setPage(newPage);
  };

  const handleChangeRowsPerPage = (
    event: React.ChangeEvent<HTMLInputElement>
  ) => {
    setRowsPerPage(+event.target.value);
    setPage(0);
  };

  return (
    <Paper sx={{ width: "100%", overflow: "hidden" }}>
      <TableContainer sx={{ maxHeight: 440 }}>
        <Table stickyHeader aria-label="sticky table">
          <TableHead>
            <TableRow>
              {columns.map((column) => (
                <TableCell
                  key={column.id}
                  align={column.align}
                  style={{ minWidth: column.minWidth }}
                >
                  {column.label}
                </TableCell>
              ))}
            </TableRow>
          </TableHead>
          <TableBody>
            {rows
              .slice(page * rowsPerPage, page * rowsPerPage + rowsPerPage)
              .map((row) => {
                return (
                  <TableRow
                    hover
                    role="checkbox"
                    tabIndex={-1}
                    key={row.activeLabels}
                  >
                    {columns.map((column) => {
                      const value = row[column.id];
                      return (
                        <TableCell key={column.id} align={column.align}>
                          {column.format && typeof value === "number"
                            ? column.format(value)
                            : value}
                        </TableCell>
                      );
                    })}
                  </TableRow>
                );
              })}
          </TableBody>
        </Table>
      </TableContainer>
      <TablePagination
        rowsPerPageOptions={[10, 25, 100]}
        component="div"
        count={rows.length}
        rowsPerPage={rowsPerPage}
        page={page}
        onPageChange={handleChangePage}
        onRowsPerPageChange={handleChangeRowsPerPage}
      />
    </Paper>
  );
}
